/* main-header-styles */
.header-main {
    position: fixed;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 64px;
    background-color: var(--primary-blue);
    /* padding: 20px; */
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
}

.header-main .row {
    position: relative;
}

.drawer-body-container .row .menu-items {
    cursor: pointer;
}


/* left-menu */
.header-main .header .left-menu {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.header-main .header .profile-card {
    position: relative;
    width: 30px;
    height: 30px;
    border-radius: 6px;
    cursor: pointer;
}

.header-main .header .profile-names {
    padding-left: 10px;
}

.header-main .header .arrow-down {
    padding-left: 50px;
}

.header-main .header .profile-names h6 {
    color: var(--white);
    margin: 0 !important;
}

.header-main .header .profile-names h6 {
    font-size: 24px;
    font-weight: bold;
    /* font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-20) var(--unnamed-font-family-inter); */
}

.header-main .header .profile-names p {
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) 12px/15px var(--unnamed-font-family-inter);
}



/* search-styles */

.header-main .header .search-box {
    position: relative;
}

.header-main .header .search-box input {
    /* border-radius: 25px !important;
    padding-left: 20px;
    width: 525px; */

    border-radius: 6px !important;
    padding-left: 20px;
    width: 280px;
    box-sizing: border-box;
    font-size: 16px;
    background-image: url(/static/media/search.e8a13685.svg);
    background-repeat: no-repeat;
    background-position: right;
    background-position: right 15px top 13px;
    padding: 10px 36px 9px 25px;
    transition: width 0.4s ease-in-out;
}

.main-header-left input.form-control {
    border-radius: 6px !important;
    background-image: url(/static/media/search.e8a13685.svg);
    background-repeat: no-repeat;
    background-position: right;
    background-position: right 15px top 13px;
    transition: width 0.4s ease-in-out;
    width: 280px;
}

.main-header-left input.form-control:focus {
    width: 525px;
}

.main-header-left input.search-box-forms {
    width: 525px;
}

.header-main .header .search-box input:focus {
    width: 525px;
}

.header-main .header .search-box input::-webkit-input-placeholder {
    font-size: 14px;
    opacity: 0.4;
    padding-left: 10px;
}

.header-main .header .search-box input:-ms-input-placeholder {
    font-size: 14px;
    opacity: 0.4;
    padding-left: 10px;
}

.header-main .header .search-box input::placeholder {
    font-size: 14px;
    opacity: 0.4;
    padding-left: 10px;
}

.header-main .header .search-icon {
    position: absolute;
    top: 12px;
    left: 35em;
}


/* tab */
@media only screen and (max-width:768px) {
    .header-main .header .search-box input {
