/* Radio Box */
.rdiobox {
    font-weight: normal;
    position: relative;
    display: block;
    line-height: 18px;
}

.rdiobox span {
    padding-left: 13px;
}

.rdiobox span:before,
.rdiobox span:after {
    line-height: 18px;
    position: absolute;
}

.rdiobox span:before {
    content: '';
    width: 16px;
    height: 16px;
    background-color: #fff;
    border: 1px solid #949eb7;
    border-radius: 50px;
    top: 2px;
    left: 0;
}

.rdiobox span:after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #fff;
    border-radius: 50px;
    top: 7px;
    left: 5px;
    display: none;
}

.rdiobox span:empty {
    padding-left: 0;
    width: 16px;
    display: block;
}

.rdiobox input[type='radio'] {
    opacity: 0;
    margin: 0;
}

.rdiobox input[type='radio']:checked+span:before {
    border-color: transparent;
    background-color: #0162e8;
}

.rdiobox input[type='radio']:checked+span:after {
    display: block;
}

.rdiobox input[type='radio'][disabled]+span {
    opacity: .75;
}

.rdiobox input[type='radio'][disabled]+span:before,
.rdiobox input[type='radio'][disabled]+span:after {
    opacity: .75;
}

.rdiobox-inline {
    display: inline-block;
}

.form-group-rdiobox {
    display: flex;
    align-items: center;
}

.form-group-rdiobox .rdiobox {
    margin-bottom: 0;
}

.form-group-rdiobox .rdiobox+.rdiobox {
    margin-left: 30px;
}

.form-group-rdiobox .rdiobox span {
    padding-left: 0;
}

