/* Timeline */
/*--- Vertical --*/
.vtimeline {
    list-style: none;
    padding: 0;
    position: relative;
    margin-bottom: 20px;
}

.vtimeline:before {
    top: 0;
    bottom: 0;
    position: absolute;
    content: " ";
    width: 3px;
    background-color: #d1d4e4;
    left: 50%;
    margin-left: -1.5px;
}

.vtimeline .timeline-wrapper {
    display: block;
    margin-bottom: 20px;
    position: relative;
    width: 100%;
    padding-right: 90px;
}

.vtimeline .timeline-wrapper:before {
    content: " ";
    display: table;
}

.vtimeline .timeline-wrapper:after {
    content: " ";
    display: table;
    clear: both;
}

.vtimeline .timeline-wrapper .timeline-panel {
    border-radius: 2px;
    padding: 20px;
    position: relative;
    background: #ecf0fa;
    border-radius: 0px;
    box-shadow: none;
    width: 35%;
    margin-left: 15%;
}

.vtimeline .timeline-wrapper .timeline-panel:before {
    position: absolute;
    top: 0;
    width: 100%;
    height: 2px;
    content: "";
    left: 0;
    right: 0;
}

.vtimeline .timeline-wrapper .timeline-panel:after {
    position: absolute;
    top: 10px;
    right: -14px;
    display: inline-block;
    border-top: 14px solid transparent;
    border-left: 14px solid #ecf0fa;
    border-right: 0 solid #ecf0fa;
    border-bottom: 14px solid transparent;
    content: " ";
}

.vtimeline .timeline-wrapper .timeline-panel .timeline-title {
    font-size: 1rem;
    font-weight: 700;
    margin: 0 0 .625rem;
}

