	position: relative;
    border: 1px solid #e1e6f1;
    color: #99a6b7;
    min-height: 14px;
    background-color: #fff;
    border-radius: 0;
    margin: 0;
    height: 40px;
    line-height: 30px;

}
.SumoSelect>.CaptionCont>span {
	display: block;
	padding-right: 30px;
